@import url('https://fonts.googleapis.com/css2?family=Mochiy+Pop+One&family=Poppins:wght@300;400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
} 

:root{
  --color-bg: rgb(253, 249, 255);
  --color-bg-variant: #603F8B;
  --color-primary: #A16AE8;
  --color-primary-variant: rgba(234, 137, 154, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);
  --color-rosa-fuerte: #FD49A0;
  --color-gris: #a6a6a6;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;

  --transition:  all 400ms ease;
  

}

/*#FD49A0 Fuchsia*/
/*#A16AE8 Violet*/
/*#B4FEE7 Aqua*/
/*#603F8B Purple*/

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Poppins', sans-serif;
  background-color: var(--color-bg);
  color: rgb(58, 3, 3);
  line-height: 1.7;
  background-image: url(../src/assets/bg-texture.png);
  
}

/* ========= estilos generales =========== */

.container{
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
  font-family: 'Mochiy Pop One', sans-serif;
}

section{
  margin-top: 8rem;
 
}

section > h2, section > h5{
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
  
}

.text-light{
  color: var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white);
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background: var(--color-primary);
  color: var(--color-bg);
}

.img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/*========= media queries (medida media) ========*/

@media (max-width: 900px) {
  .container{
    width: var(--container-width-md);
  }
  section{
    margin-top: 6rem;
  }
}

/*========= media queries (medida pequeÃ±a) ========*/

@media (max-width: 500px) {
  .container{
    width: var(--container-width-sm);
  }
  section > h2{
    margin-bottom: 2rem;
  }
}